<template>
  <div class="content">
    <div class="back">
      <van-nav-bar
        title="注册"
        fixed
        @click-left="$toPage(-1)"
        >
        <van-icon name="arrow-left" size="20px" color="#666666" slot="left" />
      </van-nav-bar>
    </div>
    <!-- <div class="img">
      <img class="in_reg_image" src="../../assets/u990.png">
    </div> -->
    <!-- <van-cell-group> -->
    <van-field
      v-model="userPhone"
      placeholder="输入手机号码"
      type="tel"
      :error-message="message"
      @input="blurPhone"
      @focus="focusPhone"
    >
      <img src="..\..\assets\reg_icon\tel.png" slot="left-icon" />
    </van-field>
    <van-field
      v-model="sms"
      center
      clearable
      placeholder="输入验证码"
      type="tel"
    >
      <img src="..\..\assets\reg_icon\code.png" slot="left-icon" />
      <van-button slot="button" size="small" type="primary" @click="get_sms">
        <span v-show="!time">{{ $t("messages.regist.code") }}</span>
        <span v-show="time">{{ time }}</span>
      </van-button>
    </van-field>
    <van-field v-model="password" type="password" placeholder="设置登录密码">
      <img src="..\..\assets\reg_icon\mima.png" slot="left-icon" />
    </van-field>
    <van-field
      v-model="userName"
      type="text"
      maxlength="32"
      placeholder="输入药店营业执照名称"
    >
      <img src="..\..\assets\reg_icon\name.png" slot="left-icon" />
    </van-field>
    <van-field
      v-model="city"
      placeholder="选择药店所处城市"
      is-link
      readonly
      @click="reg_select_city"
    >
      <img src="..\..\assets\reg_icon\city.png" slot="left-icon" />
    </van-field>
    <van-field v-model="address" type="text" maxlength="64" placeholder="输入药店收货地址">
      <img src="..\..\assets\reg_icon\adress.png" slot="left-icon" />
    </van-field>
    <!-- </van-cell-group> -->
    <p class="in_reg_point">
      {{ $t("messages.regist.point") }}
    </p>
    <van-checkbox v-model="checked">
      <a href="#" @click="text">{{ $t("messages.regist.agree") }}</a>
    </van-checkbox>
    <van-button type="info" size="normal" block @click="fun_btn_regNext">
      {{ $t("messages.regist.reg_btn1") }}
    </van-button>
  </div>
</template>
<script>
import { async } from 'q';
export default {
  data() {
    return {
      userPhone: "",
      sms: "",
      password: "",
      userName: "",
      city: "",
      cityCode: "",
      address: "",
      type: "",
      checked: false,
      message: "",
      time: "",
      flag: true,
      testCode: false
    };
  },
  mounted() {
    
  },
  methods: {
    //弹出用户服务协议
    text() {
      this.$dialog.alert({
        title: "用户服务协议",
        message: this.$t("messages.http.area")
      });
    },
    //  注册下一步
    fun_btn_regNext() {
      if (this.regist_test()) {
        JNB.query({
          serverName: "userServer",
          cls: "LoginRegistrationModule",
          method: "register",
          params: {
            phone: this.userPhone,
            password: this.password,
            password2: this.password,
            smsCode: this.sms,
            type: 3,
            storeName: this.userName,
            addressCode: this.cityCode,
            address: this.address
          },
          callback: data => {
            let result = JSON.parse(data);
            if (result.code === 200) {
              this.$router.push({
                path: "/certificate",
                query: {
                  compid: result.map.compid,
                  cityCode: this.cityCode,
                  userPhone: this.userPhone
                }
              });
            } else {
              this.$toast.fail(result.message);
            }
          }
        });
      }
    },
    //  表单验证
    regist_test() {
      if (!this.userPhone || !this.password) {
        this.$toast("账号和密码不能为空！");
        return false;
      }
      if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.userPhone)) {
        this.$toast("请输入正确的手机号码！");
        return false;
      }
      if (!this.sms) {
        this.$toast("请填写验证码！");
        return false;
      }
      if (this.password.length <= 5 || this.password.length > 19) {
        this.$toast("请输入正确的密码, 要求6-18位大小写字母数字加字符组合!");

        return false;
      }
      if (!this.city || !this.address) {
        this.$toast("药店地址不能为空！");
        return false;
      }
      if (!this.checked) {
        this.$toast("请阅读《用户服务协议》");
        return false;
      }
      return true;
    },
    //  获取手机验证码
    get_sms() {
      // this.blurPhone()
      if (this.flag && this.testCode) {
          JNB.query({
            serverName: "userServer",
            cls: "LoginRegistrationModule",
            method: "obtainVerificationCode",
            params: {
              phone: this.userPhone,
              type: 2
            },
            callback: data => {
              let result = JSON.parse(data);

              if (result.code === 200) {
                this.$toast(result.data);
              } else {
                this.$toast.fail(result.data);
              }
            }
          });
        this.flag = false;
        this.time = 60;
        var timer = setInterval(() => {
          this.time--;
          if (this.time <= 0) {
            clearInterval(timer);
            this.time = "";
            this.flag = true;
          }
        }, 1000);
      }
    },
    //  选择城市
    reg_select_city() {
      JNB._requestNative("areaSelect", data => {
        const dataObj = JSON.parse(data);
        this.city = dataObj.fullName;
        this.cityCode = dataObj.code;
      });
    },
    //  手机号码验证
    blurPhone() {
      JNB.query({
        serverName: "userServer",
        cls: "LoginRegistrationModule",
        method: "checkPhoneExist",
        params: {
          phone: this.userPhone
        },
        callback: data => {
          const dataObj = JSON.parse(data);

          if (dataObj.code !== 200) {
            this.message = dataObj.message;
            this.testCode = false
          }else{
            this.message = "";
            this.testCode = true
          }
        }
      });
    },
    focusPhone() {
      this.message = "";
    }
  }
};
</script>

<style scoped>
.content {
  height: 100vh;
  background-color: #fff;
  padding: 106px;
  box-sizing: border-box;
}
/* .content .img {
  width: 100%;
}
.content .in_reg_image {
  display: block;
  width: 300px;
  margin: 25px auto;
} */
.content .van-field {
  padding: 15px;
  box-shadow: inset 0px 2px 19.11px 1.89px rgba(255, 234, 233, 0.4),
    inset 0px 2px 3px 0px rgba(255, 234, 233, 0.63);
  border-radius: 10px;
  width: 540px;
  margin: 30px auto;
  display: flex;
  align-items: center;
}
.content .van-field img {
  width: 50px;
  height: 50px;
  margin-right: 40px;
}
.content .van-field .van-button {
  width: 160px;
  height: 50px;
  padding: 0;
  background-color: #fff;
  border: none;
  font-size: 28px;
  color: rgba(237, 47, 38, 1);
}
.content .in_reg_point {
  width: 540px;
  margin: auto;
  font-size: 20px;
  color: rgba(102, 102, 102, 1);
}
.content .van-checkbox {
  width: 540px;
  font-size: 20px;
  color: rgba(102, 102, 102, 1);
  margin: 20px auto 40px;
}
.content .van-button {
  width: 540px;
  height: 88px;
  background: rgba(237, 47, 38, 1);
  border-radius: 8px;
  margin: auto;
  border: 0;
}
</style>

